.container-rubic {
	position: relative;
	height: 100px;
	width: 86px;
	transform: scale(0.5);
}

.overlay {
	position: fixed; /* Sit on top of the page content */ /* Hidden by default */
	width: 100%; /* Full width (cover the whole page) */
	height: 100%; /* Full height (cover the whole page) */
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5); /* Black background with opacity */
	z-index: 20000; /* Specify a stack order in case you're using a different order for other elements */
	display: flex;
	align-items: center;
	justify-content: center;
}

.cube {
	position: absolute;
	width: 86px;
	height: 100px;
}

.right-alert {
	background: #e79c10;
	transform: rotate(-30deg) skewX(-30deg) translate(49px, 65px) scaleY(0.86);
}

.left-alert {
	background: #d53a33;
	transform: rotate(90deg) skewX(-30deg) scaleY(0.86) translate(25px, -50px);
}

.top-alert {
	background: #1d9099;
	transform: rotate(210deg) skew(-30deg) translate(-75px, -22px) scaleY(0.86);
	z-index: 2;
}

.face-alert {
	height: 50px;
	width: 50px;
	position: absolute;
	transform-origin: 0 0;
}

.h1.w1.l1 {
	z-index: -1;
	animation-name: h1w1l1;
