/* ===== Sidebar ===== */
.sidebar {
	position: fixed;
	top: 0;
	left: -100%;
	height: 100vh;
	width: 250px;
	background: var(--primary);
	transition: var(--tran-05);
	z-index: 10;
	margin-top: 40px;
	transition: 0.4s;
	padding: 10px 14px;
	box-shadow: 0 6px 18px 0 rgb(30 30 30 / 4%);
}

