/* =========================== */

.menu-bar .bottom-content .toggle-switch {
	position: absolute;
	right: 0;
	height: 100%;
	min-width: 60px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 6px;
	cursor: pointer;
}
.toggle-switch .switch {
	position: relative;
	height: 22px;
	width: 40px;
	border-radius: 25px;
	background-color: var(--toggle-color);
	transition: var(--tran-05);
}

.switch::before {
	content: '';
	position: absolute;
	height: 15px;
	width: 15px;
	border-radius: 50%;
	top: 50%;
	left: 5px;
	transform: translateY(-50%);
	background-color: var(--sidebar-color);
	transition: var(--tran-04);
}

body.dark .switch::before {
	left: 20px;
}

.home {
	position: absolute;
	top: 0;
	top: 0;
	left: 250px;
	height: 100vh;
	width: calc(100% - 250px);
	background-color: var(--background);
	transition: var(--tran-05);
}
.home .text {
	font-size: 30px;
	font-weight: 500;
	color: var(--text);
	padding: 12px 60px;
}

@media screen and (max-width: 768px) {
	.sidebar {
		width: 0;
		padding: 0;
	}

	.toggle {
		display: none !important;
	}

	.home {
		left: 0;
		width: 100%;
	}

	.sidebar.mobile-open {
		width: 250px;
		left: 0;
		padding-top: 30px;
	}

	.sidebar.mobile-open .image-text {
		display: flex !important;
	}

	.sidebar .image-text {
		display: none !important;
	}
}

@media screen and (min-width: 768px) {
	.sidebar {
		left: 0;
	}

	.sidebar.close {
		width: 88px;
	}

	.sidebar.close ~ .home {
		left: 78px;
		height: 100vh;
		width: calc(100% - 78px);
	}
}

/* Google Fonts Import Link */

.sidebar.close {
	width: 78px;
}
.sidebar .logo-details {
	height: 60px;
	width: 100%;
	display: flex;
	align-items: center;
}
.sidebar .logo-details i {
	font-size: 30px;
	color: var(--text);
	height: 50px;
	min-width: 78px;
	text-align: center;
	line-height: 50px;
}
.sidebar .logo-details .logo_name {
	font-size: 22px;
	color: var(--text);
	font-weight: 600;
	transition: 0.3s ease;
	transition-delay: 0.1s;
}
.sidebar.close .logo-details .logo_name {
	transition-delay: 0s;
	opacity: 0;
	pointer-events: none;
}
.sidebar .nav-links {
	height: 100%;
	padding: 30px 0 150px 0;
	overflow: auto;
}
.sidebar.close .nav-links {
	overflow: visible;
}
.sidebar .nav-links::-webkit-scrollbar {
	display: none;
}
.sidebar .nav-links li {
	position: relative;
	list-style: none;
	transition: all 0.4s ease;
	border-radius: 6px;
	margin-top: 10px;
}

.sidebar .nav-links li .icon-link {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.sidebar.close .nav-links li .icon-link {
	display: block;
}
.sidebar .nav-links li i {
	height: 50px;
	min-width: 60px;
	text-align: center;
	line-height: 50px;
	color: var(--text);
	font-size: 20px;
	cursor: pointer;
	transition: all 0.3s ease;
}
.sidebar .nav-links li.showMenu i.arrow {
	transform: rotate(-180deg);
}
.sidebar.close .nav-links i.arrow {
	display: none;
}
.sidebar .nav-links li a {
	display: flex;
	align-items: center;
	text-decoration: none;
}
.sidebar .nav-links li .link_name {
	font-size: 14px;
	font-weight: 400;
	color: var(--text);
	transition: all 0.4s ease;
}

.sidebar .nav-links li span.link_name {
	max-width: 100px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.sidebar .nav-links li:hover .link_name,
.sidebar .nav-links li:hover a i {
	color: var(--text);
}

.sidebar .nav-links li .sub-menu {
	padding: 6px 6px 14px 80px;
	margin-top: -10px;
	background: var(--primary);
	display: none;
}
.sidebar .nav-links li.showMenu .sub-menu {
	display: block;
	border-radius: 6px;
}
.sidebar .nav-links li .sub-menu a {
	color: var(--text);
	font-size: 15px;
	padding: 5px 0;
	white-space: nowrap;
	opacity: 0.6;
	transition: all 0.3s ease;
}
.sidebar .nav-links li .sub-menu a:hover {
	opacity: 1;
}
.sidebar.close .nav-links li .sub-menu {
	position: absolute;
	left: 100%;
	top: -10px;
	margin-top: 0;
	padding: 10px 20px;
	border-radius: 0 6px 6px 0;
	opacity: 0;
	display: block;
	pointer-events: none;
	transition: 0s;
}
.sidebar.close .nav-links li:hover .sub-menu {
	top: 0;
	opacity: 1;
	pointer-events: auto;
	transition: all 0.4s ease;
}
.sidebar .nav-links li .sub-menu .link_name {
	display: none;
}
.sidebar.close .nav-links li .sub-menu .link_name {
	font-size: 18px;
	opacity: 1;
	display: block;
}
.sidebar .nav-links li .sub-menu.blank {
	opacity: 1;
	pointer-events: auto;
	padding: 3px 20px 6px 16px;
	opacity: 0;
	pointer-events: none;
	box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.sidebar .nav-links li:hover .sub-menu.blank {
	top: 50%;
	transform: translateY(-50%);
}
.sidebar .profile-details {
	position: fixed;
	bottom: 0;
	width: 260px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background: #1d1b31;
	padding: 12px 0;
	transition: all 0.5s ease;
}
.sidebar.close .profile-details {
	background: none;
}
.sidebar.close .profile-details {
	width: 78px;
}
.sidebar .profile-details .profile-content {
	display: flex;
	align-items: center;
}
.sidebar .profile-details img {
	height: 52px;
	width: 52px;
	object-fit: cover;
	border-radius: 16px;
	margin: 0 14px 0 12px;
	background: #1d1b31;
	transition: all 0.5s ease;
}
.sidebar.close .profile-details img {
	padding: 10px;
}
.sidebar .profile-details .profile_name,
.sidebar .profile-details .job {
	color: #fff;
	font-size: 18px;
	font-weight: 500;
	white-space: nowrap;
}
.sidebar.close .profile-details i,
.sidebar.close .profile-details .profile_name,
.sidebar.close .profile-details .job {
	display: none;
}
.sidebar .profile-details .job {
	font-size: 12px;
}

@media (max-width: 400px) {
	.sidebar.close .nav-links li .sub-menu {
		display: none;
	}
	.sidebar {
		width: 78px;
	}
	.sidebar.close {
		width: 0;
	}
	.home-section {
		left: 78px;
		width: calc(100% - 78px);
		z-index: 100;
	}
	.sidebar.close ~ .home-section {
		width: 100%;
		left: 0;
	}
}
@media screen and (max-width: 768px) {
	.sidebar {
		width: 0;
		padding: 0;
	}

	.toggle {
		display: none !important;
	}

	.home {
		left: 0;
		width: 100%;
	}

	.sidebar.mobile-open {
		width: 250px;
		left: 0;
		padding-top: 30px;
	}

	.sidebar.mobile-open .image-text {
		display: flex !important;
	}

	.sidebar .image-text {
		display: none !important;
	}

	.modal-close {
		width: 100vw;
		height: 100vh;
		z-index: 2;
		background-color: #ccc;
	}
}

@media screen and (min-width: 768px) {
	.sidebar {
		left: 0;
	}
	.sidebar.close .nav-links li .link_name {
		pointer-events: none;
		display: none;
	}
	.sidebar.close {
		width: 88px;
	}

	.sidebar.close ~ .home {
		left: 78px;
		height: 100vh;
		width: calc(100% - 78px);
	}
}
